import 'babel-polyfill'
import './common/rem.js'
import '../static/cdn/elementui2.5.4.index.css'
import '../static/cdn/swipe-2.2.13.css'
import Vue from 'vue'
import App from './App'
import router from './router'
import Swipe from '../static/cdn/swipe.index.js'
import SwipeItem from '../static/cdn/swipe-item.js'

import ElementUI from '../static/cdn/elementui2.5.4.index.js'

Vue.component(Swipe.name, Swipe)
Vue.component(SwipeItem.name, SwipeItem)
Vue.use(ElementUI)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



